<template>
  <v-app-bar class="primary" app>
      <v-toolbar-title class="white--text" v-html="'SINCHI - Registro PQRSDF'" />
      <v-spacer />
      <v-btn class="mr-10" v-html="'Peticiones an贸nimas'" @click="$emit('anonimoPet')" />
      <v-tooltip bottom v-if="session">
        <template v-slot:activator="{ on }">
          <v-btn v-on="on" class="mr-1" color="amber" fab small dark @click="closeSession()">
            <v-icon>mdi-login-variant</v-icon>
          </v-btn>
        </template>
        <span>Cerrar Sesi贸n</span>
      </v-tooltip>
  </v-app-bar>
</template>

<script>
import { getAuth } from 'firebase/auth'
export default {
  name: "HeaderNavBar",
  props: {
    session: Boolean
  },
  methods: {
    closeSession () {
      const auth = getAuth()
      auth.signOut()
        .then(() => {
          this.$router.push('/authentication')
        })
    }
  }
}
</script>

